<template>
  <div id="pcIndex">
    <div class="logdiv">
      <!-- <div class="logtitle">Login ...</div> -->
      <!-- <input type="password" placeholder="Login ..." class="loginput" v-model="pwd" @keyup="login"/> -->
      <el-form ref="ruleForm" :model="ruleForm" :rules="rules">
        <el-form-item prop="pass">
          <el-input type="password" v-model="ruleForm.pass" autocomplete="off" placeholder="Login ..." @change="submitForm('ruleForm')"></el-input>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'pcIndex',
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.ruleForm.pass !== '') {
            if (this.ruleForm.pass != 'babyily') {
              callback(new Error('请输入正确密码'));
            }
          }
          callback();
        }
      };
      return {
        ruleForm: {
          pass: ''
        },
        rules: {
          pass: [{
            validator: validatePass,
            trigger: 'blur'
          }]
        }
      }
    },
    methods: {
      login() {
        if (this.pwd == 'babyily') {
          this.$router.push('dataList')
        }
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$router.push('dataList')
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style scoped="scoped" lang="scss">
  #pcIndex {
    height: inherit;
    background-color: #e2e9ff;
    position: relative;
  }

  .logdiv {
    width: 36vw;
    position: absolute;
    left: 50%;
    top: 38%;
    transform: translate(-50%, -50%);
  }

  .logtitle {
    margin-bottom: 2rem;
    text-align: center;
    font-size: 3rem;
    color: #0d1e7f;
  }

  .loginput {
    width: 36vw;
    height: 4rem;
    border-radius: 0.3rem;
    border: 1px solid #cb9dff;
    outline: none;
    font-size: 2rem;
    box-sizing: border-box;
    padding: 0 2rem;
    box-shadow: 0px 0px 3px 1px #cb9dff;
    color: #0d1e7f;
  }

  ::-webkit-input-placeholder {
    /* WebKit browsers */
    color: #999;
  }

  >>>.el-input__inner {
    border: 1px solid #cb9dff !important;
    box-shadow: 0px 0px 4px 0.05px #cb9dff;
  }
</style>



